import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createPerson } from "../../actions/personActions";

class AddPerson extends Component {
  constructor() {
    super();
    this.state = {
      fullName: "",
      rank: "",
      company: "",
      platoon: "",
      age: "",
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onSubmit(e) {
    e.preventDefault();
    const newUser = {
      fullName: this.state.fullName,
      rank: this.state.rank,
      company: this.state.company,
      platoon: this.state.platoon,
      age: this.state.age
    };
    this.props.createPerson(newUser, this.props.history);
  }
  render() {
    const { errors } = this.state;
    return (
      <div className="ui container">
        <div className="row">
          <div className="col-md-8 m-auto">
            <h5 className="display-4 text-center">Create new person </h5>
            <hr />
            <form onSubmit={this.onSubmit} className="ui form">
              <div className="field">
                <input
                  type="text"
                  className="form-control form-control-lg "
                  placeholder="Enter Name"
                  name="fullName"
                  value={this.state.fullName}
                  onChange={this.onChange}
                />
                <p>{errors.fullName}</p>
              </div>

              <div className="field">
                <select
                  className="ui fluid search dropdown"
                  name="rank"
                  value={this.state.rank}
                  onChange={this.onChange}
                >
                  <option value="">Select rank</option>
                  <option value="Vojnik">Vojnik</option>
                  <option value="Skupnik">Skupnik</option>
                  <option value="Narednik">Narednik</option>
                  <option value="Porucnik">Porucnik</option>
                  <option value="Satnik">Satnik</option>
                  <option value="Pukovnik">Pukovnik</option>
                </select>
                <p>{errors.age}</p>
              </div>
              <div className="field">
                <select
                  className="ui fluid search dropdown"
                  name="company"
                  value={this.state.company}
                  onChange={this.onChange}
                >
                  <option value="">Choose Company</option>
                  <option value="ZAPOVJEDNIŠTVO">ZAPOVJEDNIŠTVO</option>
                  <option value="PRVA SATNIJA">PRVA SATNIJA</option>
                  <option value="DRUGA SATNIJA">DRUGA SATNIJA</option>
                </select>
                <p>{errors.company}</p>
              </div>
              <div className="field">
                <select
                  className="ui fluid search dropdown"
                  name="platoon"
                  value={this.state.platoon}
                  onChange={this.onChange}
                >
                  <option value="">Choose Platoon</option>
                  <option value="PRVI VOD">PRVI VOD</option>
                  <option value="DRUGI VOD">DRUGI VOD</option>
                  <option value="TRECI VOD">TRECI VOD</option>
                </select>
                <p>{errors.platoon}</p>
              </div>

              <div className="field">
                <input
                  type="number"
                  placeholder="Enter Age (18-65)"
                  name="age"
                  min="18"
                  max="65"
                  value={this.state.age}
                  onChange={this.onChange}
                />

                <p>{errors.age}</p>
              </div>

              <input type="submit" className="ui button" />
            </form>
          </div>
        </div>
      </div>
    );
  }
}
AddPerson.propTypes = {
  createPerson: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createPerson }
)(AddPerson);
