import React, { Component } from "react";
import { connect } from "react-redux";
import { getPersons } from "../actions/personActions";
import PropTypes from "prop-types";
import PersonItem from "./Users/PersonItem";
import CreateNewPerson from "./Users/CreateNewPerson";

class Dashboard extends Component {
  componentDidMount() {
    this.props.getPersons();
  }

  render() {
    const { persons } = this.props.person;
    return (
      <div className="ui container">
        <div className="ui search">
          <div className="ui icon input">
            <input
              className="prompt"
              type="text"
              placeholder="Search persons..."
            />
            <i className="search icon" />
          </div>
          <div className="results" />
        </div>

        <div className="ui row">
          <div className="item">
            <h5 className="display-5 text-center">
              Here you can manage persons
            </h5>
            <br />
            <CreateNewPerson />
            <br />
            <hr />{" "}
            {persons.map(person => (
              <PersonItem key={person.id} person={person} />
            ))}{" "}
          </div>
        </div>
      </div>
    );
  }
}

Dashboard.propTypes = {
  person: PropTypes.object.isRequired,
  getPersons: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  person: state.person
});

export default connect(
  mapStateToProps,
  { getPersons }
)(Dashboard);
