import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getHistory } from "../../actions/categoryActions";
import HistoryItem from "./HistoryItem";
import { Link } from "react-router-dom";

class History extends Component {
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getHistory(id);
  }

  render() {
    const { categories } = this.props.category;
    return (
      <div className="persons">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <h5 className="display-5 text-center">
                Here is a history of tasks for specific person
              </h5>
              <div>
                <Link to={`/dashboard`} className="btn btn-light">
                  Back to List of Soldiers
                </Link>
              </div>
              <br />{" "}
              {categories.map(category => (
                <HistoryItem key={category.id} category={category} />
              ))}{" "}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

History.propTypes = {
  getHistory: PropTypes.func.isRequired,
  category: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  category: state.category
});

export default connect(
  mapStateToProps,
  { getHistory }
)(History);
