import React, { Component } from "react";
import Backlog from "./Backlog";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getBoard } from "../../actions/boardActions";

class ProjectBoard extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getBoard(id);
  }

  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  render() {
    const { id } = this.props.match.params;
    const { projectTasks } = this.props.board;
    const { errors } = this.state;

    let BoardContent;

    const boardAlgorithm = (errors, projectTasks) => {
      if (projectTasks.length < 1) {
        if (errors.projectNotFound) {
          return (
            <div className="alert alert-danger text-center" role="alert">
              {errors.projectNotFound}
            </div>
          );
        } else {
          return (
            <div className="alert alert-info text-center" role="alert">
              No Project Tasks on this board
            </div>
          );
        }
      } else {
        return <Backlog project_tasks_prop={projectTasks} />;
      }
    };

    BoardContent = boardAlgorithm(errors, projectTasks);

    return (
      <div className="ui container">
        <Link to={`/addDocument/${id}`}>Create new document</Link>
        <br />
        <hr />
        {BoardContent}
      </div>
    );
  }
}

ProjectBoard.propTypes = {
  board: PropTypes.object.isRequired,
  getBoard: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  board: state.board,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getBoard }
)(ProjectBoard);
