import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { deletePerson } from "../../actions/personActions";

class PersonItem extends Component {
  onDeleteClick = id => {
    this.props.deletePerson(id);
  };

  render() {
    const { person } = this.props;

    return (
      <div className="ui container">
        <div className="ui celled list">
          <div className="item">
            <div className="right floated content">
              <Link to={`/addCategory/${person.person.id}`}>
                <i className="tasks icon" />
              </Link>
              <i
                onClick={this.onDeleteClick.bind(this, person.person.id)}
                className="trash alternate outline icon"
              >
                {" "}
              </i>
              <Link to={`/updateUser/${person.person.id}`}>
                <i className="edit outline icon" />
              </Link>
              <Link to={`/history/${person.person.id}`}>
                <i className="history icon" />
              </Link>
            </div>
            <div className="content">
              <div className="header">
                {person.person.fullName} | {person.person.rank}
              </div>
              {person.person.age} | {person.person.company} |{" "}
              {person.person.platoon} | {person.category.nameOfCategory}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

PersonItem.propTypes = {
  deletePerson: PropTypes.func.isRequired
};

export default connect(
  null,
  { deletePerson }
)(PersonItem);
