import { GET_ERRORS, GET_PERSON, GET_PERSONS, DELETE_PERSON } from "./types";
import axios from "axios";

import _ from "lodash";

export const createPerson = (person, history) => async dispatch => {
  try {
    const res = await axios.post("/api/person", person);
    history.push("/dashboard");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};

export const getPersons = () => async dispatch => {
  const res = await axios.get("/api/person/all");
  dispatch({
    type: GET_PERSONS,
    payload: res.data
  });
};

export const getPerson = (id, history) => async dispatch => {
  const res = await axios.get(`/api/person/${id}`);
  dispatch({
    type: GET_PERSON,
    payload: res.data
  });
};

export const deletePerson = id => async dispatch => {
  if (window.confirm("Zelite li obrisati korisnika? ")) {
    await axios.delete(`/api/person/${id}`);
    dispatch({
      type: DELETE_PERSON,
      payload: id
    });
  }
};
