import axios from "axios";
import {
  GET_ERRORS,
  GET_BOARD,
  GET_PROJECT_TASK,
  DELETE_PROJECT_TASK
} from "./types";

export const addNewTask = (
  officialNumber,
  projectTasks,
  history
) => async dispatch => {
  try {
    await axios.post(`/api/board/${officialNumber}`, projectTasks);
    history.push(`/projectBoard/${officialNumber}`);
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};

export const getBoard = officialNumber => async dispatch => {
  try {
    const res = await axios.get(`/api/board/${officialNumber}`);
    dispatch({
      type: GET_BOARD,
      payload: res.data
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};

export const getTask = (
  officialNumber,
  projectId,
  history
) => async dispatch => {
  try {
    const res = await axios.get(`/api/board/${officialNumber}/${projectId}`);
    dispatch({
      type: GET_PROJECT_TASK,
      payload: res.data
    });
  } catch (error) {
    history.push("/projectDashboard");
  }
};

export const updateTask = (
  officialNumber,
  projectId,
  projectTasks,
  history
) => async dispatch => {
  try {
    await axios.patch(
      `/api/board/${officialNumber}/${projectId}`,
      projectTasks
    );
    history.push(`/projectBoard/${officialNumber}`);
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};

export const deleteTask = (officialNumber, projectId) => async dispatch => {
  if (window.confirm("Are you sure you want to delete task")) {
    await axios.delete(`/api/board/${officialNumber}/${projectId}`);
    dispatch({
      type: DELETE_PROJECT_TASK,
      payload: projectId
    });
  }
};
