import React, { Component } from "react";

import "./App.css";
import Dashboard from "./component/Dashboard";
import Header from "./component/Layout/Header";
import "bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store";
import AddCategory from "./component/Category/AddCategory";
import ProjectDashboard from "./component/ProjectDashboard";
import AddProject from "./component/Projects/AddProject";
import AddDocument from "./component/Board/Tasks/AddDocument";
import ProjectBoard from "./component/Board/ProjectBoard";
import Home from "./component/Layout/Home";
import { SET_USER } from "./actions/types";
import setJWTToken from "./securityFiles/setJWTToken";
import jwt_decode from "jwt-decode";
import Login from "./component/Layout/Login";
import SecuredRoute from "./securityFiles/SecuredRoute";
import { logout } from "./actions/securityActions";
import AddPerson from "./component/Users/AddPerson";
import UpdatePerson from "./component/Users/UpdatePerson";
import GetUsers from "./component/Admins/GetUsers";
import History from "./component/Category/History";

const jwtToken = localStorage.jwtToken;

if (jwtToken) {
  setJWTToken(jwtToken);
  const decoded_jwtToken = jwt_decode(jwtToken);
  store.dispatch({
    type: SET_USER,
    payload: decoded_jwtToken
  });
  const currentTime = Date.now() / 1000;
  if (decoded_jwtToken.exp < currentTime) {
    store.dispatch(logout());
    window.location.href = "/";
  }
}
class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Header />

            <Route exact path="/login" component={Login} />
            <Route exact path="/" component={Home} />

            <Switch>
              <SecuredRoute exact path="/dashboard" component={Dashboard} />
              <SecuredRoute
                exact
                path="/projectDashboard"
                component={ProjectDashboard}
              />
              <SecuredRoute exact path="/getUsers" component={GetUsers} />
              <SecuredRoute exact path="/addUser" component={AddPerson} />

              <SecuredRoute exact path="/addProject" component={AddProject} />
              <SecuredRoute
                exact
                path="/updateUser/:id"
                component={UpdatePerson}
              />
              <SecuredRoute exact path="/history/:id" component={History} />
              <SecuredRoute
                exact
                path="/addCategory/:id"
                component={AddCategory}
              />
              <SecuredRoute
                exact
                path="/projectBoard/:id"
                component={ProjectBoard}
              />

              <SecuredRoute
                exact
                path="/addDocument/:id"
                component={AddDocument}
              />
            </Switch>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
