import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { addCategory } from "../../actions/categoryActions";
import { Link } from "react-router-dom";

class AddCategory extends Component {
  constructor(props) {
    super(props);
    const { id } = this.props.match.params;

    this.state = {
      nameOfCategory: "",
      startDate: "",
      endDate: "",
      person_id: id,
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }
  onSubmit(e) {
    e.preventDefault();
    const newCat = {
      nameOfCategory: this.state.nameOfCategory,
      startDate: this.state.startDate,
      endDate: this.state.endDate
    };
    this.props.addCategory(this.state.person_id, newCat, this.props.history);
  }

  render() {
    const { errors } = this.state;
    return (
      <div className="add-PBI">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <Link to={`/dashboard`} className="btn btn-light">
                Back to List of Soldiers
              </Link>
              <h4 className="display-4 text-center">Add Category to Person</h4>
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <select
                    className="form-control form-control-lg"
                    name="nameOfCategory"
                    value={this.state.nameOfCategory}
                    onChange={this.onChange}
                  >
                    <option value="">Name of category</option>
                    <option value="Godisnji">Godisnji</option>
                    <option value="RRV">Redovno radno vrijeme</option>
                    <option value="Bolovanje">Bolovanje</option>
                  </select>
                  <p>{errors.nameOfCategory}</p>
                </div>

                <h6>Start Date</h6>
                <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="startDate"
                    value={this.state.startDate}
                    onChange={this.onChange}
                  />
                  <p>{errors.startDate}</p>
                </div>
                <h6>End Date</h6>
                <div className="form-group">
                  <input
                    type="date"
                    className="form-control form-control-lg"
                    name="endDate"
                    value={this.state.endDate}
                    onChange={this.onChange}
                  />
                  <p>{errors.endDate}</p>
                </div>

                <input
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddCategory.propTypes = {
  addCategory: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { addCategory }
)(AddCategory);
