import {
  ADD_CATEGORY,
  GET_CATEGORY,
  GET_HISTORY_OF_USERS_CATEGORY
} from "./../actions/types";

const initialState = {
  categories: [],
  category: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case ADD_CATEGORY:
      return {
        ...state,
        category: action.payload
      };
    case GET_CATEGORY:
      return {
        ...state,
        category: action.payload
      };
    case GET_HISTORY_OF_USERS_CATEGORY:
      return {
        ...state,
        categories: action.payload
      };
    default:
      return state;
  }
}
