import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { createNewProject } from "../../actions/projectActions";

class AddProject extends Component {
  constructor() {
    super();
    this.state = {
      projectName: "",
      description: "",
      date: "",
      officialNumber: "",
      classNumber: "",
      errors: {}
    };

    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const newProject = {
      projectName: this.state.projectName,
      description: this.state.description,
      date: this.state.date,
      officialNumber: this.state.officialNumber,
      classNumber: this.state.classNumber
    };
    this.props.createNewProject(newProject, this.props.history);
  }

  render() {
    const { errors } = this.state;
    return (
      <div className="container">
        <div className="col-md-8 m-auto">
          <h5 className="display-4 text-center">Create new project </h5>
          <hr />
          <form className="ui form" onSubmit={this.onSubmit}>
            <div className="field">
              <label>Project Name</label>
              <input
                type="text"
                name="projectName"
                placeholder="Project Name"
                value={this.state.projectName}
                onChange={this.onChange}
              />
              <p>{errors.projectName}</p>
            </div>
            <div className="field">
              <label>Official Number</label>
              <input
                type="text"
                name="officialNumber"
                placeholder="1033- and two digit"
                value={this.state.officialNumber}
                onChange={this.onChange}
              />
              <p>{errors.officialNumber}</p>
            </div>
            <div className="field">
              <label>Class Number</label>
              <input
                type="text"
                name="classNumber"
                placeholder="023- and  two digit"
                value={this.state.classNumber}
                onChange={this.onChange}
              />
              <p>{errors.classNumber}</p>
            </div>
            <div className="field">
              <label>Description</label>
              <input
                type="text"
                name="description"
                placeholder="Description"
                value={this.state.description}
                onChange={this.onChange}
              />
              <p>{errors.description}</p>
            </div>
            <div className="field">
              <label>Date</label>
              <input
                type="date"
                name="date"
                placeholder="Description"
                value={this.state.date}
                onChange={this.onChange}
              />
              <p>{errors.date}</p>
            </div>

            <button className="ui button" type="submit">
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  }
}

AddProject.propTypes = {
  createNewProject: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createNewProject }
)(AddProject);
