import React, { Component } from "react";
import Document from "../Board/Tasks/Document";

class Backlog extends Component {
  render() {
    const { project_tasks_prop } = this.props;
    const tasks = project_tasks_prop.map(projectTask => (
      <Document key={projectTask.id} projectTask={projectTask} />
    ));
    let Unclassified = [];
    let Secret = [];
    let Confidentially = [];
    let Limited = [];
    for (let i = 0; i < tasks.length; i++) {
      if (tasks[i].props.projectTask.projectStatus === "Unclassified") {
        Unclassified.push(tasks[i]);
      }

      if (tasks[i].props.projectTask.projectStatus === "Secret") {
        Secret.push(tasks[i]);
      }

      if (tasks[i].props.projectTask.projectStatus === "Confidentially") {
        Confidentially.push(tasks[i]);
      }
      if (tasks[i].props.projectTask.projectStatus === "Limited") {
        Limited.push(tasks[i]);
      }
    }
    return (
      <div className="ui center aligned container ">
        <div className="ui grid">
          <div className="row">
            <div className="four wide column">
              <div className="ui card">
                <div className="card">
                  <div className="card-header">
                    <h2>Unclassified</h2>
                  </div>
                </div>
              </div>
              {Unclassified}
            </div>
            <div className="four wide column">
              <div className="ui card">
                <div className="card-header">
                  <h2>Secret</h2>
                </div>
              </div>
              {Secret}
            </div>
            <div className="four wide column">
              <div className="ui card">
                <div className="card-header">
                  <h2>Confidentially</h2>
                </div>
              </div>
              {Confidentially}
            </div>
            <div className="four wide column">
              <div className="ui card">
                <div className="card-header">
                  <h2>Limited</h2>
                </div>
              </div>
              {Limited}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Backlog;
