import React, { Component } from "react";
import { addNewTask } from "../../../actions/boardActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";

class AddDocument extends Component {
  constructor(props) {
    super(props);
    const { id } = this.props.match.params;
    this.state = {
      projectSummary: "",
      projectStatus: "",
      projectPriority: 0,
      date: "",
      officialNumber: id,
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();
    const newTask = {
      projectSummary: this.state.projectSummary,
      projectStatus: this.state.projectStatus,
      projectPriority: this.state.projectPriority,
      date: this.state.date
    };
    this.props.addNewTask(
      this.state.officialNumber,
      newTask,
      this.props.history
    );
  }

  render() {
    const { errors } = this.state;
    return (
      <div className="container">
        <div className="col-md-8 m-auto">
          <h5 className="display-4 text-center">Add new document </h5>
          <hr />
          <form className="ui form" onSubmit={this.onSubmit}>
            <div className="field">
              <label>Project summary</label>
              <input
                type="text"
                name="projectSummary"
                placeholder="Project summary"
                value={this.state.projectSummary}
                onChange={this.onChange}
              />
              <p>{errors.projectSummary}</p>
            </div>

            <div className="field">
              <label>Document status</label>
              <select
                className="ui fluid search dropdown"
                name="projectStatus"
                value={this.state.projectStatus}
                onChange={this.onChange}
              >
                <option value="">Status</option>
                <option value="Unclassified">Unclassified</option>
                <option value="Secret">Secret</option>
                <option value="Confidentially">Confidentially</option>
                <option value="Limited">Limited</option>
              </select>
            </div>
            <div className="field">
              <label>Date</label>
              <input
                type="date"
                name="date"
                placeholder="Date"
                value={this.state.date}
                onChange={this.onChange}
              />
            </div>

            <button className="ui button" type="submit">
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  }
}

AddDocument.propTypes = {
  addNewTask: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { addNewTask }
)(AddDocument);
