import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createPerson, getPerson } from "../../actions/personActions";

class UpdatePerson extends Component {
  constructor() {
    super();
    this.state = {
      id: "",
      fullName: "",
      rank: "",
      company: "",
      platoon: "",
      age: "",
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  //WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
  componentWillReceiveProps(nextProps) {
    const { id, fullName, rank, age } = nextProps.person;
    this.setState({
      id,
      fullName,
      rank,
      age
    });
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getPerson(id, this.props.history);
  }
  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }
  onSubmit(e) {
    e.preventDefault();

    const updatePerson = {
      id: this.state.id,
      fullName: this.state.fullName,
      rank: this.state.rank,
      company: this.state.company,
      platoon: this.state.platoon,
      age: this.state.age
    };
    this.props.createPerson(updatePerson, this.props.history);
  }

  render() {
    const { errors } = this.state;
    return (
      <div className="project">
        <div className="ui container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h5 className="display-4 text-center"> Update Person</h5>
              <hr />
              <form className="ui form" onSubmit={this.onSubmit}>
                <div className="field">
                  <input
                    type="text"
                    className="form-control form-control-lg "
                    placeholder="Enter full name"
                    name="fullName"
                    value={this.state.fullName}
                    onChange={this.onChange}
                  />
                  <p>{errors.fullName}</p>
                </div>

                <div className="field">
                  <select
                    className="ui fluid search dropdown"
                    name="rank"
                    value={this.state.rank}
                    onChange={this.onChange}
                  >
                    <option value="">Select rank</option>
                    <option value="Vojnik">Vojnik</option>
                    <option value="Skupnik">Skupnik</option>
                    <option value="Narednik">Narednik</option>
                    <option value="Porucnik">Porucnik</option>
                    <option value="Satnik">Satnik</option>
                    <option value="Pukovnik">Pukovnik</option>
                  </select>
                </div>

                <div className="field">
                  <select
                    className="ui fluid search dropdown"
                    name="company"
                    value={this.state.company}
                    onChange={this.onChange}
                  >
                    <option value="">Choose Company</option>
                    <option value="ZAPOVJEDNIŠTVO">ZAPOVJEDNIŠTVO</option>
                    <option value="PRVA SATNIJA">PRVA SATNIJA</option>
                    <option value="DRUGA SATNIJA">DRUGA SATNIJA</option>
                  </select>
                  <p>{errors.company}</p>
                </div>
                <div className="field">
                  <select
                    className="ui fluid search dropdown"
                    name="platoon"
                    value={this.state.platoon}
                    onChange={this.onChange}
                  >
                    <option value="">Choose Platoon</option>
                    <option value="PRVI VOD">PRVI VOD</option>
                    <option value="DRUGI VOD">DRUGI VOD</option>
                    <option value="TRECI VOD">TRECI VOD</option>
                  </select>
                  <p>{errors.platoon}</p>
                </div>

                <div className="field">
                  <input
                    type="number"
                    placeholder="Enter Age (18-65)"
                    name="age"
                    min="18"
                    max="65"
                    value={this.state.age}
                    onChange={this.onChange}
                  />

                  <p>{errors.age}</p>
                </div>

                <input type="submit" className="ui button" />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdatePerson.propTypes = {
  getPerson: PropTypes.func.isRequired,
  createPerson: PropTypes.func.isRequired,
  person: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  person: state.person.person,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getPerson, createPerson }
)(UpdatePerson);
