import React, { Component } from "react";
import { Link } from "react-router-dom";
import { deleteTask } from "../../../actions/boardActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";

class Document extends Component {
  onDeleteClick(officialNumber, projectId) {
    this.props.deleteTask(officialNumber, projectId);
  }
  render() {
    const { projectTask } = this.props;

    return (
      <div className="ui container">
        <div className="row">
          <div className="ui card">
            <div className="card">
              <div className="card-header">
                Summary - {projectTask.projectSummary}
                <div className="description">
                  Official Number - {projectTask.officialNumber}
                </div>
                <div className="description">
                  Class Number - {projectTask.projectSequence}
                </div>
                <div className="description">{projectTask.description}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
Document.propTypes = {
  deleteTask: PropTypes.func.isRequired
};

export default connect(
  null,
  { deleteTask }
)(Document);
