import axios from "axios";

import {
  GET_ERRORS,
  GET_CATEGORY,
  GET_HISTORY_OF_USERS_CATEGORY
} from "./types";

export const addCategory = (id, category, history) => async dispatch => {
  try {
    await axios.post(`/api/category/${id}`, category);
    history.push(`/dashboard`);
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};
export const getCategory = id => async dispatch => {
  const res = await axios.get(`/api/category/${id}`);
  dispatch({
    type: GET_CATEGORY,
    payload: res.data
  });
};

export const getHistory = id => async dispatch => {
  try {
    const res = await axios.get(`/api/category/history/${id}`);
    dispatch({
      type: GET_HISTORY_OF_USERS_CATEGORY,
      payload: res.data
    });
  } catch (error) {
    dispatch({
      type: GET_ERRORS,
      payload: error.response.data
    });
  }
};
